@import './mixins.scss';
@import './vars.scss';

.wrapper {
    width: 100%;
    height: 100%;
}

.container {
    @include container(1230px, 15px);
    margin-left: 113px;
}

body {
    @include appFont('Barlow',$mdtxtfz,$mdfw);
    background-color: $appBg;
    color: $appTxtCl;
    user-select: none;
    &::-webkit-scrollbar{
        width: 0;
        background-color: transparent;
    }
}